import React, { Component } from 'react';
import * as THREE from 'three';
import 'three/examples/js/loaders/MTLLoader';
import 'three/examples/js/loaders/OBJLoader';
import { Link } from 'react-router-dom';

import initThree from '../../utils/initThree';

import geo from './models/Building_Distribution_Networks.obj';
import mtl from './models/Building_Distribution_Networks.mtl';

import './style.scss';

class Home extends Component {
	constructor(props) {
		super(props);
		this.state = {
			loaded: false,
		};
	}

	componentDidMount() {
		// init three
		const { stats, renderer, scene, camera, controls } = initThree(this.canvas, {
			camera: {
				z: 5,
			},
			axisHelper: false,
		});
		this.renderer = renderer;
		this.stats = stats;

		const spotLight = new THREE.SpotLight(0xff0000);
		spotLight.angle = 25 * (Math.PI / 180);
		spotLight.position.set(40, 40, 0);
		spotLight.castShadow = true;
		spotLight.distance = 200;
		spotLight.decay = 2;
		spotLight.penumbra = 0.9;
		scene.add(spotLight);

		const mtlLoader = new THREE.MTLLoader();
		mtlLoader.load(mtl, materials => {
			materials.preload();
			const objLoader = new THREE.OBJLoader();
			objLoader.setMaterials(materials);
			objLoader.load(geo, object => {
				object.traverse(node => {
					if (node.material) node.material.side = THREE.DoubleSide;
				});
				object.rotation.set(0, -Math.PI, 0);
				object.position.set(0, 0, 30);
				scene.add(object);
				this.setState({ loaded: true });
			});
		});

		const animate = timestamp => {
			this.rafID = requestAnimationFrame(animate);
			stats.begin();
			// update the time uniform
			// uniforms.time.value = timestamp;
			renderer.render(scene, camera);
			stats.end();
		};
		requestAnimationFrame(animate);
	}

	componentWillUnmount() {
		this.renderer.dispose();
		this.canvas.outerHTML = '';
		this.stats.domElement.outerHTML = '';
		delete this.canvas;
		delete this.stats.domElement;
		window.cancelAnimationFrame(this.rafID);
	}

	render() {
		return (
			<div>
				<canvas
					id="canvas"
					ref={canvas => {
						this.canvas = canvas;
					}}
				/>
			</div>
		);
	}
}

export default Home;
